import { Component } from '@angular/core';

@Component({
    selector: 'checkbox-usage',
    template: require('./checkbox.component.html'),
    styles: [require('./checkbox.component.css')]
})
export class CheckboxComponent {
    checked = false;
    indeterminate = false;
    align = 'start';
    disabled = false;
}


